          <div class="right-text">
              本人从大三开始学习前端，接触后便爱上了这个职业，
              <br>
              有着些许处女座完美主义的我，渴望将最好的网页展现给用户，
              <br>              
              大四毕业通过校招来到上海，四月到十一月全职就职于某公司。
              <br>              
              怎奈公司经营不善，导致裁员。
              <br>                          
              我想说的是，虽然我只有近一年工作经验，
              <br>              
              但是由于在校的学习，以及自己浓厚的兴趣，自己可以胜任这个职位
              <br>
              希望能得到一份能够优雅打代码的工作！
          </div>
