<template>
    <section class="animated">
        <img class="me" src="../assets/me.jpg" alt="me" width="100" height="150">
        <div class="detail">
            <hr>
        </div>
        <div class="page1">
            <div style="margin-left:9%;width:10px;height:10px;border-radius:50%;background-color:yellow;margin-top:5px;"></div>
            <div style="padding:5px;width:15%;margin-left:2%;background-color:#FFA500;color:#fff">
                <h3>2016年3月始</h3>
                <div>
                    开始接触前端，利用学校图书馆资源，网上视频，教程，学习前端基础
                </div>
            </div>
        </div>
         <div class="page2">
            <div style="margin-left:9%;width:10px;height:10px;border-radius:50%;background-color:yellow;margin-top:5px;"></div>
            <div style="padding:5px;width:15%;margin-left:2%;background-color:#FFA500;color:#fff;">
                 <h3>2017年4月-10月</h3>
                <div>
                    来到上海，并在波导信息技术有限公司实习，由于基础较好，进公司，没有培训，
                    便开始进入项目之中，全职实习
                </div>
            </div>
        </div>
        <div class="page3">
            <div style="margin-left:9%;width:10px;height:10px;border-radius:50%;background-color:yellow;margin-top:5px;"></div>
            <div style="padding:5px;width:15%;margin-left:2%;background-color:#fff;background-color:#FFA500;color:#fff">
                <h3>2017年7月</h3>
                <div>
                    安徽大学毕业，并取得学士学位，毕业证书
                    在上海波导公司转正，参与更多的项目
                </div>
            </div>
        </div>
        <div class="page4">
            <div style="margin-left:9%;width:10px;height:10px;border-radius:50%;background-color:yellow;margin-top:5px;"></div>
            <div style="padding:5px;width:15%;margin-left:2%;background-color:#fff;background-color:#FFA500;color:#fff">
                <h3>2017年11月</h3>
                <div>
                    上海波导公司经营不善，濒临倒闭。
                    目前待业中。。。
                    希望伯乐能够发现我，嘿嘿！
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
  name: "blogHome",
  data() {
    return {};
  },
  mounted() {},
  components: {},
  methods: {}
};
</script>


<style scoped>
section {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #d15fee;
  overflow: hidden;
}
li {
  float: left;
}
@keyframes move {
  20% {
    left: 15%;
  }
  40% {
    left: 33%;
  }
  60% {
    left: 51%;
  }
  80% {
    left: 67%;
  }
  100% {
    left: 80%;
  }
}
@keyframes move2 {
  20% {
    opacity: 0.2;
  }
  40% {
    opacity: 0.4;
  }
  60% {
    opacity: 0.6;
  }
  80% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pagemove {
  from {
    opacity: 0;
    top: 70%;
  }
  to {
    opacity: 1;
    top: 30%;
  }
}
.me {
  position: absolute;
  left: 0;
  top: 5%;
  animation: move 5s;
  animation-fill-mode: forwards;
}
.detail {
  position: absolute;
  top: 30%;
  width: 100%;
  opacity: 0;
  animation: move2 5s;
  animation-fill-mode: forwards;
}
.page1 {
  position: absolute;
  left: 9%;
  width: 100%;
  opacity: 0;
  animation: pagemove 2s;
  /* animation-delay: 1s; */
  animation-fill-mode: forwards;
}
.page2 {
  position: absolute;
  left: 27%;
  width: 100%;
  opacity: 0;
  animation: pagemove 2s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
}
.page3 {
  position: absolute;
  left: 45%;
  width: 100%;
  opacity: 0;
  animation: pagemove 2s;
  animation-delay: 2s;
  animation-fill-mode: forwards;
}
.page4 {
  position: absolute;
  left: 63%;
  width: 100%;
  opacity: 0;
  animation: pagemove 2s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}
</style>


